<template>
  <!--渲染表单-->
    <el-form ref="form" class="process-form" label-position="top" :rules="rules" :model="_value">

        <el-form-item
                v-if="(item.name !== 'SpanLayout' && item.name !== 'Description')&&(!item.props.perm||item.props.perm!='H')"
                :prop="item.id" :label="item.title" v-for="(item, index) in forms" :key="item.name + index">

            <form-design-render :ref="`sub-item_${item.id}`" v-model="_value[item.id]" mode="PC" :config="item"/>
        </el-form-item>
        <form-design-render ref="span-layout" v-else-if="!item.props.perm||item.props.perm!='H'" v-model="_value"
                            mode="PC" :config="item"/>
    </el-form>
</template>

<script>
import FormDesignRender from '@/views/admin/layout/form/FormDesignRender'
import {ValueType} from "@/views/common/form/ComponentsConfigExport";



export default {
    name: "FormRender",
    components: {FormDesignRender},
    props: {
        forms: {
            type: Array,
            default: () => {
                return []
            }
        },
        value: {
            type: Object,
            default: () => {
                return {}
            }
        }
    },
    data() {
        return {
            rules: {},
        }
    },
    created() {
        this.loadFormConfig(this.forms)
    },
    computed: {
        _value: {
            get() {
                return this.value
            },
            set(val) {

                this.$emit('input', val)
            }
        }
    },
    methods: {
        validate(call) {
            let success = true
            this.$refs.form.validate(valid => {
                success = valid
                if (valid) {
                    //校验成功再校验内部
                    for (let i = 0; i < this.forms.length; i++) {
                        if (this.forms[i].name === 'TableList') {
                            let formRef = this.$refs[`sub-item_${this.forms[i].id}`]
                            if (formRef && Array.isArray(formRef) && formRef.length > 0) {
                                formRef[0].validate(subValid => {
                                    success = subValid
                                })
                                if (!success) {
                                    break
                                }
                            }
                        }
                    }
                }
                call(success)
            });
        },
        loadFormConfig(forms) {
            forms.forEach(item => {
                var props = item.props;
                if (item.name === 'SpanLayout') {
                    this.loadFormConfig(props.items)
                } else {
                    this.$set(this._value, item.id, this.value[item.id])
                    var ruleArray = [];
                    if (props.required) {
                        //必填
                        ruleArray.push({
                            type: item.valueType === 'Array' ? 'array' : undefined,
                            required: true,
                            message: `请填写${item.title}`, trigger: 'blur'
                        })

                    }
                    if (props.minLength && props.minLength >= 0 && (item.valueType === ValueType.string)) {
                        //最小长度
                        ruleArray.push({

                            min: props.minLength,
                            message: `长度不能小于${props.minLength}`,
                            trigger: 'blur'
                        })
                    }
                    if (props.maxLength && props.maxLength >= 0 && (item.valueType === ValueType.string)) {
                        //最大长度
                        ruleArray.push({

                            max: props.maxLength,
                            message: `长度不能大于${props.maxLength}`,
                            trigger: 'blur'
                        })
                    }
                    if (props.regex && props.regex.length > 0 && (item.valueType === ValueType.string) ) {
                        //正则表达式

                        var r = (rule, value, callback) => {
                            if (!value || value.length < 1) {
                                return callback();
                            }

                            const reg = new RegExp(props.regex);
                            if(reg.test(value)){
                                return callback();
                            }
                            callback(new Error(props.regexMsg));
                        };
                        ruleArray.push(
                            {validator: r, trigger: 'blur'}
                        )
                    }

                    //数字
                    if( (item.valueType === ValueType.number) ){
                        var r = (rule, value, callback) => {
                            if (!value || value.length < 1) {
                                return callback();
                            }

                            var regex=/^-?([0-9]\d*\.?\d*)|(0\.\d*[1-9])$/
                            if(!regex.test(value)){
                                return callback(new Error('请输入数字'));
                            }

                            //数字最小值

                            if(props.minNum){
                                if(parseFloat(value)<props.minNum){
                                    return callback(new Error('表单值不能小于'+props.minNum));
                                }
                            }    //数字最大值

                            if(props.maxNum){
                                if(parseFloat(value)>props.maxNum){
                                    return callback(new Error('表单值不能大于'+props.maxNum));
                                }
                            }
                            //判断小数点位数
                            if(props.precision){
                                var xs = value.toString().split(".");

                                if(xs.length==1){
                                    return callback();
                                }
                                if(xs[1].length>props.precision){
                                    return callback(new Error('小数位数不能超过'+props.precision));

                                }

                            }

                            return callback();
                        };
                        ruleArray.push(
                            {validator: r, trigger: 'blur'}
                        )
                    }


                    if (ruleArray.length > 0) {
                        this.$set(this.rules, item.id, ruleArray)

                    }
                }
            })
        }
    }
}
</script>

<style lang="less" scoped>
.process-form {
  /deep/ .el-form-item__label {
    padding: 0 0;
  }
}
</style>
